.main-navbar {
    border-bottom: 1px solid #ccc;
}

.main-navbar .top-navbar {
    background-color: #2874f0;
    padding-top: 10px;
    padding-bottom: 10px;
}

.main-navbar .top-navbar .brand-name {
    color: #fff;
}

.main-navbar .top-navbar .nav-link {
    color: #fff;
    font-size: 16px;
    font-weight: 500;
}

.main-navbar .top-navbar .dropdown-menu {
    padding: 0px 0px;
    border-radius: 0px;
}

.main-navbar .top-navbar .dropdown-menu .dropdown-item {
    padding: 8px 16px;
    border-bottom: 1px solid #ccc;
    font-size: 14px;
}

.main-navbar .top-navbar .dropdown-menu .dropdown-item i {
    width: 20px;
    text-align: center;
    color: #2874f0;
    font-size: 14px;
}

.main-navbar .navbar {
    padding: 0px;
    background-color: white;
}

.main-navbar .navbar .nav-item .nav-link {
    padding: 0;
    margin: 8px 20px;
    color: #000;
    font-size: 15px;
}

#md-filter {
    display: none;
}

#offcanvasWithBothOptions {
    display: none;
}

.wishlist{
    position: relative;
}

.wishlist a svg{
    font-size: 1.2rem;
}

.wishlist-number{
    position: absolute;
    font-size: 9px;
    top: -4px;
    right: 8px;
}
@media only screen and (max-width: 992px) {
    #md-filter {
        display: block;
    }

    #offcanvasWithBothOptions {
        display: block;
    }
}

@media only screen and (max-width: 600px) {
    .main-navbar .top-navbar .nav-link {
        font-size: 12px;
        padding: 8px 10px;
    }
}

